import React, { ReactElement, useState, useContext, RefObject, useMemo } from 'react';
import cx from 'classnames';

import styles from './css/core.module.css';
import { MdExitToApp } from 'react-icons/md';
import { SetModeContext, SettingsContext } from '../Contexts';
import { TournamentModes } from '../interfaces';
import Dropdown from './components/Dropdown';
import { isDev } from '../utils/electron';

interface Button {
	name: string
	icon: ReactElement
	color?: string
	onClick?: () => void
	href?: string
}
interface DropdownProps {
	resolve: (v: string) => void
	dropdown: boolean
	current: string
	options: {[key: string]: string} | {[key: string]: { name: string }} | (string | null)[]
}
interface CoreProps {
	id: TournamentModes
	title?: string
	buttons?: (Button | DropdownProps)[]
	short?: boolean
	ref?: RefObject<HTMLDivElement>
	childRef?: RefObject<HTMLDivElement>
	className?: string
	childClass?: string
	scrollable?: boolean
	children: ReactElement | ReactElement[]
}

export default function Core(props: CoreProps) {

	const setMode = useContext(SetModeContext);
	const settings = useContext(SettingsContext);

	const pages = useMemo(() => {
		return {
			pasteInput: {
				name: 'Smart Input mode',
			},
			export: {
				name: 'Export mode',
				hide: !isDev
			},
			settings: {
				name: 'Detailed settings view'
			},
			crossTable: {
				name: 'Cross-table'
			},
			announcements: {
				name: 'Announcements',
				hide: !settings.public
			},
			knockout: {
				name: 'Knockout pairing view',
				hide: settings.pairingSystem !== 'knockout'
			},
			sort: {
				name: 'Quick Drag & Drop'
			}
		} as {[key in TournamentModes]: {
			name: string,
			hide?: boolean
		}};
	}, [settings]);

	return (
		<div className={cx(styles.stage, {[styles.short]: props.short}, props.className)} ref={props.ref}>
			<div className={styles.titleContainer}>
				<div className={styles.titleWrapper}>
					<div className={styles.titles}>
						<Dropdown
							current={props.id}
							options={pages as any}
							title={props.title}
							resolve={setMode}
							className={cx('button', styles.button)}
						/>
						{props.buttons?.map((b, i) => {
							if ('dropdown' in b) return <Dropdown
								{...b}
								className={cx('button', styles.button)}
							/>;
							if (b.href) return <a href={b.href} target='_blank' rel='noopener noreferrer' key={cx(b.name, i)} className={cx('button', styles.button)} >
								{React.cloneElement(b.icon, { ...b.icon.props, style: {
									...b.icon.props.style,
									color: b.color
								}})}
								{b.name}
							</a>;
							return <div key={cx(b.name, i)} className={cx('button', styles.button)} onClick={b.onClick}>
								{React.cloneElement(b.icon, { ...b.icon.props, style: {
									...b.icon.props.style,
									color: b.color
								}})}
								{b.name}
							</div>;
						})}
						<div className={cx('button', styles.closeButton)} onClick={() => setMode('')}>
							<MdExitToApp style={{color: 'indianred'}} />Back to tournament
						</div>
					</div>
				</div>
			</div>
			<div ref={props.childRef} className={cx(styles.body, props.childClass, {scrollable: props.scrollable !== false}, 'scrollable-x')}>
				{props.children}
			</div>
		</div>
	);
}