import React, { ReactElement, useCallback, useMemo, useContext, useState, useReducer, Dispatch, CSSProperties } from 'react';
import styles from './css/dock.module.css';
import { TournamentStatus, TournamentSettings, DockOption, ResultObject, TournamentModes } from '../interfaces';
import { MdExitToApp } from 'react-icons/md';
import {  FaTh, FaPauseCircle, FaPlayCircle, FaFilter, FaAngleDoubleDown, FaStickyNote, FaFileExport, FaGripLines, FaSyncAlt, FaFileImport, FaCogs, FaBullhorn, FaBomb, FaArrowsAlt, FaSkullCrossbones, FaSitemap, FaSortAlphaUp } from 'react-icons/fa';
import { LoadedContext, AlertContext } from '../Contexts';
import { TiFlowSwitch } from 'react-icons/ti';
import { POST } from '../utils/requests';
import { Result } from '../resources/result';
import { TernaryInput } from '../components/Inputs';
import { isDev } from '../utils/electron';
import { IconType } from 'react-icons/lib';
import Sort from '../screen/Sort';
import Player from '../models/player';
import { px } from '../utils/prototype';

interface DockProps {
	setMode: (m: TournamentModes) => void
	userRound: number
	status: TournamentStatus
	settings: TournamentSettings
	closeTournament: () => void
	lastRoundOverride: boolean
	setLastRound: (override: boolean) => void
	triggerError: (message: string) => void
	switchPairings: (set?: boolean) => void
	switchMode: boolean
	load: () => void
	canReset: boolean
	resetColumns: () => void
	pairingFilters: {[key: string]: boolean | undefined }
	setFilters: (v: {[key: string]: boolean}) => void
	nextOutstanding: Dispatch<unknown>
	canSetOutstanding: boolean
	players: Player[]
	updatePlayerDict: () => Promise<void>
}

export const filterPairingOptions = [
	{
		key: 'link',
		name: 'Link',
		func: (r: Result): boolean => !!r.link,
		show: (s: TournamentSettings) => !!s.source
	},
	{
		key: 'started',
		name: 'Started',
		func: (r: Result): boolean => !!r.fetched?.startTime,
		show: (s: TournamentSettings) => !!s.pullResults
	},
	{
		key: 'unfinished',
		name: 'Unfinished',
		func: (r: Result, o: ResultObject): boolean => o[0] == null && o[1] == null
	}
] as DockOption[];

export default function Dock(props: DockProps): ReactElement {

	const round = useMemo(() => props.userRound || props.status.round, [props.userRound, props.status.round]);
	let lastRound = false;
	if (typeof props.lastRoundOverride !== 'undefined') lastRound = props.lastRoundOverride;
	else lastRound = !props.status.active;
	const hasLoaded = useContext(LoadedContext);

	const { setAlert } = useContext(AlertContext);
	const undoRoundPairings = useCallback(() => {
		setAlert({
			type: 'confirm',
			message: 'Really undo all pairings for this round?',
			resolve: () => POST({
				url:['tournament', props.status.id, 'undoRoundPairings'].join('/'),
				data: {
					round: props.status.round
				}
			}).then(() => {
				props.load();
			}),
		});
	}, [props.status.id, props.status.round, props.load, props.switchPairings, setAlert]);
	const destroyTournament = useCallback(() => {
		let displayName = props.status.name || props.status.id;
		setAlert({
			type: 'confirm',
			message: 'Delete tournament \'' + displayName + '\'?\nPlease note this action cannot be undone.',
			resolve: () => POST({
				url: ['tournament', props.status.id, 'closeAuthorization'].join('/'),
				data: {
					round: props.status.round
				}
			}).then(props.closeTournament),
		});
	}, [props.status.id, props.status.round, props.closeTournament, props.status.name, setAlert]);

	const [visibleOptions, setOptions] = useReducer((acc: string, curr: string) => {
		if (acc === curr) return '';
		return curr;
	}, '');

	const [knockoutPlayers, setKnockoutPlayers] = useState([] as Player[]);

	const data = [
		{
			icon: FaFileImport,
			color: 'lightblue',
			name: 'Smart Import Players',
			onClick: () => props.setMode('pasteInput')
		},
		{
			name: 'Export Mode',
			icon: FaFileExport,
			color: 'lightblue',
			inactive: !isDev,
			onClick: () => props.setMode('export')
		},
		{
			name: 'Detailed Settings View',
			icon: FaCogs,
			onClick: () => props.setMode('settings')
		},	
		{
			name: 'Crosstable',
			onClick: () => props.setMode('crossTable'),
			icon: FaTh,
			color: 'green',
			inactive: !props.status.round
		},
		{
			name: 'Announcements',
			icon: FaBullhorn,
			onClick: () => props.setMode('announcements'),
			inactive: !props.settings.public
		},
		{
			name: 'Knockout View',
			icon: FaSitemap,
			onClick: () => props.setMode('knockout'),
			inactive: props.settings.pairingSystem !== 'knockout' || !props.status.round
		},
		{
			name: 'Quick Drag & Drop',
			icon: <FaSortAlphaUp />,
			onClick: () => props.setMode('sort')
		},
		'divider',
		{
			name: lastRound ? 'Resume Tournament' : 'Pause Tournament',
			onClick: () => {
				if (lastRound && props.status.round >= props.settings.totalRounds) props.triggerError('Increase the number of rounds in Settings to continue!');
				else props.setLastRound(!lastRound);
			},
			icon: lastRound ? FaPlayCircle : FaPauseCircle,
			color: 'yellow'
		},
		{
			name: 'Refresh',
			icon: FaSyncAlt,
			color: 'lightblue',
			onClick: props.load,
			hidden: false
		},
		{
			name: !props.switchMode ? 'Switch or Undo Pairings' : 'Back',
			icon: <TiFlowSwitch style={{color: '#385898', strokeWidth: 1.5}} />,
			active: props.switchMode,
			onClick: () => props.switchPairings(),
			inactive: ((): boolean => {
				switch (props.settings.pairingSystem) {
				case 'swiss':
					if (!props.status.round) return true;
					if (!props.status.active) return true;
					return false;
				case 'round-robin':
					return false;
				case 'knockout':
					return props.status.round > 1;
				default:
					return true;
				}
			})()
		},
		{
			name: 'Undo Last Round Pairings',
			icon: <FaBomb style={{ color: 'indianred' }} />,
			onClick: undoRoundPairings
		},
		{
			name: 'Convert to Knockout Finals',
			icon: FaSitemap,
			onClick: () => {
				setAlert({
					title: <>Please group your {props.settings.competitors === 'individual' ? 'players' : 'teams'} into brackets</>,
					type: 'confirm',
					message: <Sort
						className={styles.sort}
						updatePlayerDict={props.updatePlayerDict}
						mode='pools'
						poolName='Bracket'
						players={props.players.map(p => {
							p.pool = null;
							return p;
						})}
						setPlayers={setKnockoutPlayers}
					/>,
					resolve: () => POST({
						url: px('tournament', props.status.id, 'startKnockout'),
						data: {
							players: knockoutPlayers
						}
					}).then(props.load),
					tall: true,
					wide: true
				});
			}
		},
		{
			name: 'Jump to Next Unfinished Pairing',
			icon: FaAngleDoubleDown,
			color: 'lightgrey',
			inactive: !props.canSetOutstanding,
			onClick: props.nextOutstanding
		},
		{
			name: 'Filter Pairings',
			icon: FaFilter,
			color: 'darkgreen',
			ternaryOptions: true,
			onClick: () => setOptions('Filter Pairings'),
			active: !!visibleOptions,
			options: filterPairingOptions
		},
		{
			name: 'Player Details',
			icon: FaStickyNote,
			color: 'orange',
			onClick: () => setOptions('Player Details'),
			hidden: true,
			inactive: true,
			options: [
				{
					name: 'Name',
					locked: true,
					defaultValue: true
				},
				{
					name: 'Score'
				},
				{
					name: 'Board'
				},
				{
					name: 'Rating'
				}
			] as DockOption[]
		},
		{
			name: 'Reset Layout',
			icon: FaArrowsAlt,
			color: 'darkgrey',
			onClick: props.resetColumns,
			inactive: !props.canReset
		},
		'divider',
		{
			name: 'Destroy Tournament',
			icon: FaSkullCrossbones,
			color: 'indianred',
			style: { strokeWidth: 1.5 },
			onClick: destroyTournament
		},
		{
			name: 'Exit to Menu',
			icon: <MdExitToApp style={{ color: 'indianred', strokeWidth: 1.5 }} />,
			onClick: props.closeTournament
		}
	] as {
		name: string,
		icon: ReactElement | IconType,
		color?: string
		style?: CSSProperties
		hidden?: boolean
		active?: boolean
		inactive?: boolean
		onClick: () => void
		ternaryOptions?: boolean
		options: DockOption[]
	}[];

	return (
		<div className={styles.dock}>
			<div className={[styles.stage, 'scrollable'].join(' ')}>
				<div className={[styles.column].join(' ')}>
					{data.map((b, i) => {
						if (typeof b === 'string') {
							return <div key={['divider', i].join('.')} className={styles.container}>
								<FaGripLines />
							</div>;
						} else {
							if (b.hidden) return null;
							return <div key={[b.name, i].join('.')} className={[styles.container, b.active ? styles.active : ''].join(' ')}>
								<div className={['button', styles.button, hasLoaded && b.inactive ? 'null' : ''].join(' ')} onClick={b.onClick}>
									{!hasLoaded?
										null :
										React.isValidElement(b.icon) ?
											React.cloneElement(b.icon, { ...b.icon.props, style: {
												...b.icon.props.style,
												...b.style,
												color: b.color
											}}) :
											<b.icon style={{ ...b.style, color: b.color }} />
									}
								</div>
								<div className={styles.name}>
									{b.name}
								</div>
								{b.options ?
									<div className={[styles.options, visibleOptions === b.name ? styles.visible : ''].join(' ')}>
										<div className={styles.optionsTitle}>{b.name}</div>
										{b.options.map((o, j) => {
											return <div key={['option', i, j].join('.')}>
												<TernaryInput
													isTernary={b.ternaryOptions}
													defaultValue={props.pairingFilters[o.key]}
													setValue={(v) => props.setFilters({ [o.key]: v})}
												/>
												<label htmlFor={o.name}>{o.name}</label>
											</div>;
										})}
									</div> :
									null
								}
							</div>;
						}
					})}
				</div>
			</div>
		</div>
	);

}